// app/page.tsx
"use client";
import { useState, useMemo } from 'react';
import { useFinance } from '@/context/FinanceContext';
import TransactionForm from '@/components/forms/TransactionForm';
import AccountsSidebar from '@/components/dashboard/AccountsSidebar';
// import ComplexFinanceChart from '@/components/dashboard/ComplexFinanceChart'; // (PENDIENTE CREAR)
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Dialog, DialogContent, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { PlusCircle, MinusCircle, TrendingDown, TrendingUp, PiggyBank, Wallet } from 'lucide-react';
import { startOfMonth, endOfMonth, isSameMonth, isSameYear, isSameDay, format } from 'date-fns';
import { es } from 'date-fns/locale';

export default function Dashboard() {
  const { transactions, categories } = useFinance();
  const [period, setPeriod] = useState('month');
  const [dateFilter, setDateFilter] = useState(new Date()); // Fecha base para filtrar

  // ------------------- LÓGICA DE FILTRADO Y CÁLCULOS -------------------
  const filteredTx = useMemo(() => {
    return transactions.filter(t => {
        // Filtrar las que son "proyectadas" (amount 0) para no afectar los totales reales visuales,
        // a menos que queramos ver una proyección. Por ahora, mostremos solo lo real > 0 o ingresos.
        // OJO: Si un gasto fijo no se ha pagado, su amount es 0. ¿Lo mostramos? Sí, para saber que está pendiente.
        const tDate = new Date(t.date);
        if (period === 'month') return isSameMonth(tDate, dateFilter);
        if (period === 'year') return isSameYear(tDate, dateFilter);
        if (period === 'day') return isSameDay(tDate, dateFilter);
        return true;
      }).sort((a,b) => new Date(b.date).getTime() - new Date(a.date).getTime()); // Ordenar por fecha descendente
  }, [transactions, period, dateFilter]);

  // Cálculos de KPI (Key Performance Indicators)
  const incomeReal = filteredTx.filter(t => t.type === 'income').reduce((acc, curr) => acc + curr.amount, 0);
  // Para gastos, sumamos el amount REAL pagado.
  const expenseReal = filteredTx.filter(t => t.type === 'expense').reduce((acc, curr) => acc + curr.amount, 0);
  
  // Cálculo de Ahorro (Ingresos en cuentas de ahorro o categorías específicas)
  // Basado en tu excel, el ahorro parece ser una transferencia a una cuenta especifica o un rubro.
  // Usaremos la categoría especial interna 'cat_sys_savings' y otras que definas como ahorro.
  const savingsReal = filteredTx.filter(t => t.type === 'income' && (t.categoryId === 'cat_sys_savings' || t.categoryId === 'cat_ahorro_meta'))
                                .reduce((acc, curr) => acc + curr.amount, 0);

  const remaining = incomeReal - expenseReal; // Restante simple (Flujo de caja)

  // Porcentajes (Evitando división por cero)
  const expensePct = incomeReal > 0 ? (expenseReal / incomeReal) * 100 : 0;
  const savingPct = incomeReal > 0 ? (savingsReal / incomeReal) * 100 : 0;


  return (
    <>
    {/* Sidebar derecho flotante en desktop */}
    <div className="hidden md:block fixed right-0 top-0 bottom-0 w-[320px] z-30 pt-4">
        <AccountsSidebar className="h-full bg-zinc-900/90 backdrop-blur-md border-l border-zinc-800 rounded-l-xl shadow-2xl" />
    </div>

    <div className="space-y-6 pb-24">
      {/* Encabezado y Botones de Acción (Top Left) */}
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
        <div>
            <h1 className="text-3xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400 tracking-tight">
            HOME FINANCE
            </h1>
            <p className="text-sm text-zinc-400">{format(dateFilter, "EEEE, d 'de' MMMM yyyy", { locale: es })}</p>
        </div>
        
        <div className="flex gap-3 w-full md:w-auto">
            {/* Botón Ingreso */}
            <Dialog>
                <DialogTrigger asChild>
                    <Button className="flex-1 md:flex-none bg-emerald-600 hover:bg-emerald-700 text-white shadow-lg shadow-emerald-900/30 transition-all hover:scale-105">
                        <PlusCircle className="mr-2 h-5 w-5" /> Agregar Ingreso
                    </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800 sm:max-w-[500px]">
                    <CardHeader className="px-0 pt-0"><CardTitle>Registrar Ingreso</CardTitle></CardHeader>
                    <TransactionForm defaultType="income" />
                </DialogContent>
            </Dialog>

            {/* Botón Gasto */}
            <Dialog>
                <DialogTrigger asChild>
                    <Button className="flex-1 md:flex-none bg-rose-600 hover:bg-rose-700 text-white shadow-lg shadow-rose-900/30 transition-all hover:scale-105">
                        <MinusCircle className="mr-2 h-5 w-5" /> Agregar Gasto
                    </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-950 border-zinc-800 sm:max-w-[500px]">
                    <CardHeader className="px-0 pt-0"><CardTitle>Registrar Gasto</CardTitle></CardHeader>
                    <TransactionForm defaultType="expense" />
                </DialogContent>
            </Dialog>
        </div>
      </div>


      {/* ZONA DE GRÁFICA COMPLEJA (Pendiente de implementar componente real) */}
      <Card className="bg-zinc-900/50 border-zinc-800 shadow-xl relative overflow-hidden">
        <div className="absolute inset-0 bg-gradient-to-r from-indigo-500/5 to-purple-500/5 pointer-events-none"/>
        <CardHeader>
            <CardTitle>Resumen Financiero Visual</CardTitle>
            <CardDescription>Comparativa de Ingresos vs Gastos y Tendencia de Ahorro</CardDescription>
        </CardHeader>
        <CardContent className="h-[300px] flex items-center justify-center text-zinc-500 italic border-2 border-dashed border-zinc-800 rounded-md m-4">
           {/* <ComplexFinanceChart transactions={transactions} period={period} currentDate={dateFilter} /> */}
           (Aquí irá la Gráfica Avanzada con Recharts en el próximo paso)
        </CardContent>
      </Card>

      
      {/* Tabs de Periodo y Resúmenes */}
      <Tabs defaultValue="month" onValueChange={setPeriod} className="space-y-6">
        <div className="flex justify-between items-center">
            <TabsList className="bg-zinc-900 border border-zinc-800 p-1">
            <TabsTrigger value="day" className="data-[state=active]:bg-zinc-800">Día</TabsTrigger>
            <TabsTrigger value="month" className="data-[state=active]:bg-zinc-800">Mes</TabsTrigger>
            <TabsTrigger value="year" className="data-[state=active]:bg-zinc-800">Año</TabsTrigger>
            </TabsList>
            
            {/* Aquí podrías poner un selector de fecha para cambiar el mes/año que se visualiza */}
        </div>


        <TabsContent value={period} className="space-y-6 animate-in fade-in slide-in-from-bottom-4">
          {/* KPIs Cards */}
          <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
            <KPICard title="Ingresos Reales" value={incomeReal} icon={<TrendingUp className="text-emerald-400 h-6 w-6" />} colorClass="text-emerald-400" bgClass="bg-emerald-950/30" borderClass="border-emerald-900/50" />
            <KPICard title="Gastos Reales" value={expenseReal} icon={<TrendingDown className="text-rose-400 h-6 w-6" />} colorClass="text-rose-400" subtext={`${expensePct.toFixed(1)}% del ingreso`} bgClass="bg-rose-950/30" borderClass="border-rose-900/50"/>
            <KPICard title="Ahorro Generado" value={savingsReal} icon={<PiggyBank className="text-blue-400 h-6 w-6" />} colorClass="text-blue-400" subtext={`${savingPct.toFixed(1)}% del ingreso`} bgClass="bg-blue-950/30" borderClass="border-blue-900/50"/>
            <KPICard title="Restante (Flujo)" value={remaining} icon={<Wallet className="text-purple-400 h-6 w-6" />} colorClass={remaining >= 0 ? "text-white" : "text-red-500"} bgClass="bg-purple-950/30" borderClass="border-purple-900/50"/>
          </div>

          {/* Tabla Detallada de Movimientos */}
          <Card className="bg-zinc-900 border-zinc-800 shadow-lg">
            <CardHeader className="flex flex-row items-center justify-between">
                <div>
                    <CardTitle>Movimientos del Periodo</CardTitle>
                    <CardDescription>Detalle de transacciones esperadas y reales</CardDescription>
                </div>
                {/* <ExportDataBtn data={filteredTx} /> (Pendiente) */}
            </CardHeader>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                  {/* Aquí va una tabla avanzada de shadcn/ui. Por brevedad, uso un div styled */}
                  <div className="min-w-full divide-y divide-zinc-800 text-sm">
                    {filteredTx.length === 0 ? (
                      <p className="text-zinc-500 text-center py-8">No hay datos para este periodo.</p>
                    ) : (
                      filteredTx.map(tx => {
                        const category = categories.find(c => c.id === tx.categoryId);
                        const account = useFinance().accounts.find(a => a.id === tx.accountId); // Acceso directo al contexto dentro del map (no ideal por performance pero funcional)
                        
                        // Lógica para mostrar estado del gasto (Pagado vs Pendiente vs Ahorrado)
                        let statusTag = null;
                        if (tx.type === 'expense') {
                            if (tx.amount === 0 && tx.isFixed) {
                                statusTag = <span className="px-2 py-0.5 rounded-full bg-yellow-500/20 text-yellow-300 text-xs">Pendiente de pago</span>;
                            } else if (tx.budgetedAmount && tx.amount < tx.budgetedAmount && tx.hasGeneratedSavings) {
                                statusTag = <span className="px-2 py-0.5 rounded-full bg-green-500/20 text-green-300 text-xs">Ahorro generado</span>;
                            } else if (tx.budgetedAmount && tx.amount > tx.budgetedAmount) {
                                statusTag = <span className="px-2 py-0.5 rounded-full bg-red-500/20 text-red-300 text-xs">Sobrecosto</span>;
                            }
                        }

                        return (
                        <div key={tx.id} className="flex flex-col md:flex-row justify-between items-start md:items-center p-4 hover:bg-zinc-800/50 transition-colors gap-2">
                          <div className="flex gap-3 items-center flex-1">
                            <div className={`h-10 w-1 rounded-full ${tx.type === 'income' ? 'bg-emerald-500' : 'bg-rose-500'}`}></div>
                            <div>
                              <div className="flex items-center gap-2">
                                <p className="font-bold text-zinc-200">{tx.description || category?.name}</p>
                                {statusTag}
                              </div>
                              
                              <div className="flex flex-wrap gap-x-2 text-xs text-zinc-400">
                                <span>{format(new Date(tx.date), 'dd/MM/yy')}</span> | 
                                <span className="text-indigo-300">{category?.name} {tx.subCategoryId && `(${tx.subCategoryId})`}</span> |
                                <span>{account?.bank} ({account?.name})</span>
                              </div>
                            </div>
                          </div>

                          <div className="flex flex-col items-end ml-4 md:ml-0">
                            {/* Monto Real */}
                            <span className={`text-lg font-bold font-mono ${tx.type === 'income' ? 'text-emerald-400' : 'text-rose-400'}`}>
                              {tx.type === 'income' ? '+' : '-'} ${tx.amount.toLocaleString()}
                            </span>
                            
                            {/* Comparativa con Presupuesto (si existe y es diferente) */}
                            {tx.type === 'expense' && tx.budgetedAmount && tx.budgetedAmount !== tx.amount && (
                                <div className="text-xs text-zinc-500 text-right mt-1">
                                    Esp: <span className="line-through">${tx.budgetedAmount.toLocaleString()}</span>
                                    <span className={tx.amount < tx.budgetedAmount ? "text-green-400 ml-1" : "text-red-400 ml-1"}>
                                        (Dif: {tx.amount < tx.budgetedAmount ? '+' : ''}${(tx.budgetedAmount - tx.amount).toLocaleString()})
                                    </span>
                                </div>
                            )}
                          </div>
                        </div>
                      )})
                    )}
                  </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
    </>
  );
}

// Componente auxiliar para las tarjetas de KPI con mejor diseño
function KPICard({ title, value, icon, colorClass, subtext, bgClass, borderClass }: any) {
  return (
    <Card className={`backdrop-blur-sm ${bgClass} border ${borderClass} shadow-lg relative overflow-hidden group`}>
       <div className={`absolute -right-4 -top-4 opacity-10 group-hover:opacity-20 transition-opacity rotate-12 scale-150 ${colorClass}`}>{icon}</div>
      <CardContent className="p-5 flex flex-col justify-between h-full relative z-10">
        <div className="flex justify-between items-center mb-3">
          <span className="text-zinc-300 font-medium text-sm uppercase tracking-wider">{title}</span>
          <div className={`p-2 rounded-lg bg-black/20 ${colorClass}`}>{icon}</div>
        </div>
        <div>
          <span className={`text-2xl lg:text-3xl font-extrabold tracking-tight font-mono ${colorClass}`}>
            ${value.toLocaleString('es-CO')}
          </span>
          {subtext && <p className="text-xs font-medium text-zinc-400 mt-2 flex items-center gap-1">{subtext}</p>}
        </div>
      </CardContent>
    </Card>
  );
}